.CreateTodoButton {
    background-color: 
    #4FA095;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 54px;
    right: 54px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    transform: rotate(0);
    transition: .3s ease;

    z-index: 1;
  }
  
  .CreateTodoButton:hover {
    transform: scale(.9);
  }